.membership-home-container {
  height: 100vh;
  background: var(--bg-secondary, #f5f7fa);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.home-content {
  flex: 1;
  padding: 24px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.welcome-card {
  .header-content {
    display: flex;
    align-items: center;
    gap: 16px;
    width: 100%;
    padding: 16px;

    .user-icon {
      font-size: 64px;
      width: 64px;
      height: 64px;
      color: #f59e0b;
    }

    .user-info {
      flex: 1;

      h1 {
        margin: 0;
        font-size: 28px;
        font-weight: 600;
        color: #1f2937;
      }

      .user-name {
        margin: 4px 0 0;
        font-size: 18px;
        color: #6b7280;
      }
    }
  }

  mat-card-content {
    padding: 24px;

    .user-details {
      display: flex;
      flex-direction: column;
      gap: 16px;

      .detail-item {
        display: flex;
        align-items: center;
        gap: 12px;
        padding: 12px;
        border-radius: 8px;
        background: #f9fafb;
        transition: background 0.2s;

        &:hover {
          background: #f3f4f6;
        }

        mat-icon {
          color: #f59e0b;
        }

        .detail-content {
          display: flex;
          flex-direction: column;
          gap: 4px;

          .label {
            font-size: 12px;
            color: #6b7280;
            font-weight: 500;
          }

          .value {
            font-size: 16px;
            color: #1f2937;
            font-weight: 600;
          }
        }
      }
    }
  }

  mat-card-actions {
    padding: 16px 24px;
    display: flex;
    gap: 12px;
    flex-wrap: wrap;

    button {
      flex: 1;
      min-width: 120px;

      mat-icon {
        margin-right: 8px;
      }
    }
  }
}

.quick-actions {
  h2 {
    margin: 0 0 16px;
    font-size: 24px;
    font-weight: 600;
    color: #1f2937;
  }

  .action-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 16px;

    .action-card {
      padding: 24px;
      text-align: center;
      cursor: pointer;
      transition: all 0.3s;

      &:hover {
        transform: translateY(-4px);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
      }

      mat-icon {
        font-size: 48px;
        width: 48px;
        height: 48px;
        color: #f59e0b;
        margin-bottom: 12px;
      }

      h3 {
        margin: 0 0 8px;
        font-size: 18px;
        font-weight: 600;
        color: #1f2937;
      }

      p {
        margin: 0 0 16px;
        font-size: 14px;
        color: #6b7280;
        line-height: 1.5;
      }

      button {
        width: 100%;
      }
    }
  }
}

@media (max-width: 768px) {
  .membership-home-container {
    padding: 16px;
  }

  .welcome-card {
    .header-content {
      flex-direction: column;
      text-align: center;
    }

    mat-card-actions {
      flex-direction: column;

      button {
        width: 100%;
      }
    }
  }

  .quick-actions .action-grid {
    grid-template-columns: 1fr;
  }
}
